@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@500&display=swap);
.new-btn{
    cursor: pointer;
    height: 1rem;
    width: 1rem;
    margin-left: 0.25rem;
}

.send-btn{
    cursor: pointer;
    height: 1.2rem;
    width: 1.2rem;
    margin-left: 0.25rem;
}

.wrapper{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    width: 100%;
}
.input-wrapper{
    width: 0%;
    overflow: hidden;
    transition: width 0.5s;
}

.input-wrapper.open{
    width: 90%;
}

.c-input{
    width: 90%;
    border: 1px solid lightgrey;
    border-radius: 0.25rem;
    padding: 0.5rem;
}

.c-input:focus{
    border: 0.5px solid var(--dark-blue);
    box-shadow: 0 0 2px var(--dark-blue);
}


@media screen and (max-width: 480px){
    .new-btn{
        height: 1.4rem;
        width: 1.4rem;
    }

    .send-btn{
        height: 1.6rem;
        width: 1.6rem;
    }
    .c-input{
        font-size: 1.2rem;
    }

}
.card{
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 0 0px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 0.5rem;
    margin: 0.5rem 0.25rem;
}

.card hr{
    margin: 0.2rem 0;
    opacity: 0.4
}

.card-footer small{
    font-size: 0.55rem;
    font-family: Roboto-Light;
    color: #8a8a8a;
}

.card-body{
    font-family: Roboto-Regular;
    font-size: 0.9rem;
    color: var(--dark-blue)
}

.card-body.rtl{
    direction: rtl;
    font-family: 'Open Sans', sans-serif;
}

.card-remove{
    font-family: Roboto-Regular;
    font-size: 0.8rem;
    position: absolute;
    right: 1rem;
    cursor: pointer;
    color: var(--dark-blue);
}


@media screen and (max-width: 480px){

    .card-body, 
    .card-remove,
    .card-footer .reaction > span{
        font-size: 1.2rem;
    }
    .card-footer .reaction{
        margin-right: 1.2rem;
    }

    .card-footer .reaction img{
        width: 1.2rem;
        height: 1.2rem;
    }
    .card{
        margin: 0.8rem  0px;
    }
    .card hr{
        margin: 0.5rem  0px;
    }
}
.reaction{
    align-items: center;
    display: inline-flex;
    cursor: pointer;
    margin-right: 8px;
}
.reaction img{
    height: 1rem;
    width: 1rem;
    transition: all .2s ease-in-out;
}

.reaction img[alt="dislike"],.reaction img[alt="like"]{
    height: 0.85rem;
    width: 0.85rem;
}

.reaction span{
    margin-right: 0.1rem;
    color: #aaa;
    font-family: Roboto-Light;
    font-size:small;
}

.reaction:hover img{
    transform: scale(1.2);
}
.col {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding: 0.5rem 1rem ;
    overflow: hidden;
    height: calc(50% - 1rem);
}

.top{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.col .title{
    font-family: Roboto-Regular;
    color: var(--dark-blue);
    font-size: 0.9rem
}

.col .icon{
    height: 1rem;
    width: 1rem;
    margin-right: 0.2rem;
}

.body {
    position: relative;
    height: 100%;
    overflow: auto;
}
/* width */
.body::-webkit-scrollbar {
    width: 5px;
}
  

.body::-webkit-scrollbar-track {
    background: #f1f1f1; 
}
   
/* Handle */
.body::-webkit-scrollbar-thumb {
    background: var(--dark-purple); 
}
  
/* Handle on hover */
.body::-webkit-scrollbar-thumb:hover {
    /* background: #555;  */
    background: var(--dark-purple); 
}


@media screen and (max-width: 480px) {
    .col .title{
        font-size: 1.2rem
    }

    .col .icon{
        height: 1.2rem;
        width: 1.2rem;
        margin-right: 0.2rem;
    }
    .col{
        height: unset;
    }   
}
.grid{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    height: 100%;
}

.grid > div{
    flex-basis: calc(50% - 0.1rem - 2rem);  
    overflow: hidden;
}

.grid > div:nth-child(1){
    border-right: 0.25px solid #ddd;
    border-bottom: 0.25px solid #ddd;
}

.grid > div:nth-child(2){
    border-left: 0.25px solid #ddd;
    border-bottom: 0.25px solid #ddd;
}

.grid > div:nth-child(3){
    border-right: 0.25px solid #ddd;
    border-top: 0.25px solid #ddd;
}

.grid > div:nth-child(4){
    border-left: 0.25px solid #ddd;
    border-top: 0.25px solid #ddd;
}


@media only screen and (max-width: 480px){
    .grid{
        overflow: hidden;
        overflow-x: scroll;    
        flex-wrap: unset;
    }
    
    .grid > div{
        border-right: 1px solid #ddd !important;
        border-bottom: 0px !important;
        flex-basis: 1 !important;
        width: calc(100vw - 2rem);
        max-height: unset;
        flex: none;
    }
}
